import wx from "weixin-js-sdk";
//var isLogin = false;
export const initWechatJs = (
  appId,
  nonceStr,
  timestamp,
  signature,
  router,
  link,
  imgUrl
) => {
  wx.config({
    debug: false,
    appId: appId,
    timestamp: timestamp,
    nonceStr: nonceStr,
    signature: signature,
    jsApiList: [
      "checkJsApi",
      "chooseImage",
      "uploadImage",
      "onMenuShareTimeline",
      "getLocalImgData",
      "onMenuShareAppMessage",
    ]
  });
  wx.ready(function() {
    wx.onMenuShareTimeline({
      title: "COACH", // 
      link: link, // JS
      imgUrl: imgUrl, // 
      success: function() {
        console.log();
      },
      cancel: function() {
        alert("");
      }
    });
    wx.onMenuShareAppMessage({
      title: "COACH", // 
      desc: "COACH", // 
      link: link, // 
      imgUrl: imgUrl, // 
      success: function success(res) {
        // alert('');
        // alert(uuu);
      },
      cancel: function cancel(res) {
        //alert('');
      },
      fail: function fail(res) {
        alert(JSON.stringify(res));
      }
    });
  });
  wx.error(function(res) {
    alert("");
  });
};

export const openCameraForPic = function(type, uploadCallback) {
  var sourceType = [];
  if (type === 1) {
    sourceType.push("album");
  } else {
    sourceType.push("camera");
  }
  wx.chooseImage({
    count: 1, // 9
    sizeType: ["original", "compressed"], // 
    sourceType: sourceType, // 
    success: function(res) {
      var localIds = res.localIds; // IDlocalIdimgsrc
      console.log(localIds);
      if (uploadCallback && typeof uploadCallback === "function") {
        uploadCallback(localIds);
      }
    },
    error: function(e) {
      alert(e);
    }
  });
};



